import { useEffect, useState } from 'react';
import css from './About.module.css';
import AOS from 'aos';
import 'aos/dist/aos.css'; //
import svg from '../../images/symbol-defs.svg';
// import { Parallax } from 'react-parallax'

export const About = ({ yOffset }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [currentText, setCurrentText] = useState('');
  const [imageClass, setImageClass] = useState(0);
  const [cursorVisible, setCursorVisible] = useState(true);

  useEffect(() => {
    switchClassName();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentIndex, yOffset]);

  let delay = 0;
  console.log(delay);
  const switchClassName = () => {
    let resultClass;
    switch (currentIndex) {
      case 0:
        resultClass = css.container;
        break;
      case 1:
        resultClass = css.containerFirst;
        break;
      case 2:
        resultClass = css.containerTwo;
        break;
      case 3:
        resultClass = css.containerThree;
        break;
      case 4:
        resultClass = css.containerFour;
        break;
      default:
        break;
    }
    return resultClass;
  };
  const switchImageClass = () => {
    let resultClass;
    switch (imageClass) {
      case 0:
        resultClass = css.leftSide;
        break;
      case 1:
        resultClass = css.leftSideFirst;
        break;
      case 2:
        resultClass = css.leftSideTwo;
        break;
      case 3:
        resultClass = css.leftSideThree;
        break;
      case 4:
        resultClass = css.leftSideFour;
        break;
      default:
        break;
    }
    return resultClass;
  };
  const textArr = [
    '   ,     ,     ,     ,        .  - ?         .',
    '  ,            .           ,      .     ,       ,      .',
    '             .       . ,        ,  ,  300 usd,      100-150 usd,          .  ',
    ' "" ,   ,  ,           .          .          . ',
  ];
  const content = [
    { title: '', id: 1 },
    { title: '', id: 2 },
    { title: '', id: 3 },
    { title: '', id: 4 },
  ];
  AOS.init();

  return (
    // <Parallax></Parallax>
    //style={{ transform:`translateY(-${yOffset/10}vh)`,overflow:'hidden' }}
    <div className={switchClassName()}>
      <div className={switchImageClass()}>
        {content.map(({ title, id }) => {
          return (
            <h2
            className={css.title}
              data-aos="fade"
              // data-aos-anchor-placement="bottom-bottom"
              data-aos-delay={id * 100}
              data-aos-duration="700"
              style={{
                transform:
                  yOffset > 600 ? `translateX(0)` : `translateX(-800px)`,
              }}
              onMouseLeave={() => {
                setCurrentIndex(0);
                setCurrentText('');
                setImageClass(0);
              }}
              key={id}
              onMouseMove={() => {
                setImageClass(id);
                setCurrentIndex(id);
                setCurrentText(textArr[id - 1]);
                setCursorVisible(false);
              }}
              
            >
              {id === 1 && (
                <svg
                  className={
                    cursorVisible ? css.cursorIcon : css.cursorIconHidden
                  }
                  data-aos="fade-right"
                  data-aos-delay={3000}
                >
                  <use href={`${svg}#icon-cursor`} />
                </svg>
              )}

              {/* <div className={css.timeline}></div> */}
              {title}
            </h2>
          );
        })}
      </div>
      <div className={css.rightSide}>
        <p className={css.rightSideText}>{currentText}</p>
      </div>
    </div>
  );
};
